    background-size: cover;
    border-radius: 30px;
    overflow: hidden;
    padding: 395px 20px 20px 20px;
}

@media (min-device-pixel-ratio: 2),
(min-resolution: 192dpi),
(min-resolution: 2dppx) {
    .main-bg {
        background-image: linear-gradient(180deg,
                rgba(17, 17, 17, 0) 0%,
                rgba(17, 17, 17, 0.4) 100%),
            url(../images/mobile/chocolate-covered-cocoa-powder@2x.jpg);
    }
}

.button_style {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 41px;
    padding: o;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
    color: var(--color_wh);
    border-radius: 100px;
    border: none;
}

.main-text {
    padding-right: 10px;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.2;
    letter-spacing: -0.03em;
    color: var(--color_wh);
}

.main-butt {
    display: inline-flex;
    justify-content: center;
    gap: 14px;
    margin-top: 32px;
}

.main-scroll-block {
    display: inline-flex;
    align-items: center;
    margin-top: 32px;
}

.main-scroll {
    display: inline-flex;
    margin-right: 12px;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.17;
    letter-spacing: -0.03em;
    color: var(--color_wh);
}

.main-scroll-cont {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    background-color: var(--color_wh);
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.main-scroll-block:hover .main-scroll,
.main-scroll-block:focus .main-scroll {
    color: var(--bg-button);
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.main-scroll-block:focus .main-scroll-cont,
.main-scroll-block:hover .main-scroll-cont {
    background-color: var(--bg-button);
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.main-scroll-icon {
    width: 16px;
    height: 15px;
    fill: var(--bg-button);
    stroke: var(--bg-button);
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1),
        stroke 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.main-scroll-block:hover .main-scroll-icon,
.main-scroll-block:focus .main-scroll-icon {
    fill: var(--color_wh);
    stroke: var(--color_wh);
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1),
        stroke 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.buynow {
    margin-left: auto;
    margin-right: auto;
    width: 120px;
    background: var(--bg-button);
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.howmade {
    width: 159px;
    background-color: transparent;
    outline: 1px solid var(--color_wh);
    transition: outline 250ms cubic-bezier(0.4, 0, 0.2, 1),
        color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.orange_text {
    color: var(--bg-button);
}

.buynow:hover,
.buynow:focus {
    color: var(--bg-button);
    background-color: var(--bg-button_hover);
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.howmade:hover,
.howmade:focus {
    outline: 1px solid var(--bg-button);
    color: var(--bg-button);
    background-color: var(--color_wh);
    transition: outline 250ms cubic-bezier(0.4, 0, 0.2, 1),
        color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

