    transform: translateX(0);
  }

  .mobile-nav-menu {
    margin-bottom: auto;
    margin-left: 50px;
  }

  .mobile-menu-container > .mobile-nav-menu {
    margin-bottom: auto;
  }

  .mobile-nav-item:not(:last-child) {
