    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    position: relative;
  }

  .mobile-menu-link:hover, .mobile-menu-link:focus {
    color: var(--main-color);
  }

  .mobile-menu-link:after {
    content: "";
    width: 24px;
    height: 1px;
    background-color: var(--main-color);
    opacity: 0;
    transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: absolute;
    top: 100%;
    left: 0;
  }

  .mobile-menu-link:hover:after, .mobile-menu-link:focus:after {
    opacity: 1;
  }

  .social-mobile-list {
    justify-content: flex-start;
    gap: 18px;
    margin-left: 20px;
    display: flex;
  }

  .social-icon {
    width: 22px;
    height: 22px;
    stroke: #fff;
    fill: none;
    margin: 0 auto;
    transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
  }

  .social-list-link:hover .social-icon, .social-list-link:focus .social-icon {
    stroke: var(--main-color);
  }
}

@media screen and (min-width: 1200px) {
